body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.nav {
  background-color: #006989; }
  .nav > ul {
    display: -webkit-flex;
    display: flex;
    list-style-type: none;
    margin: 0; }
    .nav > ul :hover {
      background-color: #007090; }
    .nav > ul > a {
      padding: 16px;
      text-decoration: none; }
      .nav > ul > a > li {
        color: #EAEBED; }

body {
  background: #EAEBED; }

.hidden {
  padding: 25px; }


.card {
  width: 400px;
  margin-bottom: 50px;
  background-color: white;
  color: #2E2E2E;
  box-shadow: 0 10px 6px -6px #777; }
  .card__title {
    margin-top: -10 px; }
  .card__content {
    margin: 15px;
    padding: 15px; }
  .card__description {
    overflow-y: scroll;
    height: 100px; }
  .card__btn {
    background-color: #14b2a1;
    text-align: center;
    padding: 15px;
    border-radius: 7px;
    margin-top: 30px; }
    .card__btn > a {
      text-decoration: none;
      color: #EAEBED; }

.title {
  margin-top: 50px;
  margin-left: 45px;
  color: #2E2E2E; }

.section__content {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-template-rows: 1fr;
  justify-items: center;
  margin-top: 50px; }

.form {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 50vw; }

.form input, .form select {
  height: 30px;
  color: black; }




